@charset "UTF-8";
/* Variables */
@import url("https://fonts.googleapis.com/css?family=Noto+Sans+TC:300,400&display=swap&subset=chinese-traditional");
@import url("https://fonts.googleapis.com/css?family=Noto+Serif+TC:400,500,600&display=swap");
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

* {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
  color: #170000;
}

html {
  position: relative;
}

body {
  font-family: "Noto Sans TC", "Source Sans Pro", "Microsoft JhengHei", "微軟正黑體", sans-serif;
  font-family: "微軟正黑體" \0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
  overflow-x: hidden;
  background: #f9f2e8;
}

body.fixed {
  height: 100%;
  overflow: hidden;
}

main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

a {
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  background: transparent;
  outline: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
select,
button,
textarea {
  vertical-align: middle;
  outline: 0;
}

img {
  vertical-align: top;
  max-width: 100%;
}

.clear {
  clear: both;
  width: 0;
  height: 0px;
  margin: 0;
  padding: 0;
  border: none;
  overflow: hidden;
}

.fb-page,
.fb-page span,
.fb-page span iframe[style] {
  width: 100% !important;
}

.ie8-wrap {
  display: none;
}

button {
  cursor: pointer;
}

/*通用全站都有的寫法*/
*:link,
*:visited,
*:hover,
*:active,
*:focus {
  transition: color 0.25s linear, background-color 0.25s linear, border-color 0.25s linear, padding 0.15s linear, filter 0.15s linear, opacity 0.15s linear, transform 0.25s linear;
}

.break {
  display: none;
}

.pc {
  display: block;
}

.mb {
  display: none;
}

@media screen and (max-width: 767px) {
  .mb {
    display: block;
  }
  .pc {
    display: none;
  }
}

/* Modules */
@keyframes img_shine {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes font-shine {
  0% {
    background-position: 0% 0;
  }
  to {
    background-position: -300% 0;
  }
}

@keyframes flare_shine {
  0% {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}

@keyframes btn-shine {
  0% {
    transform: translateX(-150%);
  }
  70% {
    transform: translateX(120%);
  }
  100% {
    transform: translateX(120%);
  }
}

@keyframes btn-shine2 {
  0% {
    transform: translateY(-150%);
  }
  70% {
    transform: translateY(120%);
  }
  100% {
    transform: translateY(120%);
  }
}

@keyframes scrollMove {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(15px);
  }
}

@keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 160px;
  }
}

@keyframes lineMb {
  0% {
    width: 0;
  }
  100% {
    width: 100px;
  }
}

@keyframes lineMb30 {
  0% {
    width: 0;
  }
  100% {
    width: 30px;
  }
}

@keyframes backgroundScale {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1.2);
  }
  100% {
    transform: scale(1, 1);
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

[data-aos="text-down"] {
  opacity: 0;
  transform: translateY(-25px);
  transition: all 0.3s;
}

[data-aos="text-down"].aos-animate {
  transform: translateY(0);
  opacity: 1;
  transition: all 0.3s;
}

[data-aos="text-up"] {
  opacity: 0;
  transform: translateY(25px);
  transition: all 0.3s;
}

[data-aos="text-up"].aos-animate {
  transform: translateY(0);
  opacity: 1;
  transition: all 0.3s;
}

[data-aos="scale-photo"] {
  opacity: 0;
  transform: scale(1);
  transition: all 0.5s;
}

[data-aos="scale-photo"].aos-animate {
  transform: scale(1.1);
  opacity: 1;
  transition: all 0.5s;
}

[data-aos="move-left"] {
  opacity: 0;
  transform: translateX(25px);
  transition: all 0.5s;
}

[data-aos="move-left"].aos-animate {
  transform: translateX(0);
  opacity: 1;
  transition: all 0.5s;
}

[data-aos="backgroundScale"] {
  transform: scale(1);
  transition: all 10s;
}

[data-aos="backgroundScale"].aos-animate {
  transform: scale(1.05);
  transition: all 10s;
}

@keyframes lineani {
  0% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}

.video-container {
  position: relative;
  padding-bottom: 54.25%;
  padding-top: 25px;
  height: 0;
  overflow: hidden;
}

.video-container > iframe, .video-container > object, .video-container > embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.shine-font {
  background: -webkit-linear-gradient(left, #8c2b1f 0%, #341210 24%, #8c2b1f 36%, #341210 46%, #8c2b1f 52%, #341210 58%, #341210 67%, #8c2b1f 100%);
  background-size: 100%;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  background-size: 300%;
  -webkit-animation: font-shine 10s infinite;
  animation: font-shine 10s infinite;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.shine-font2 {
  background: -webkit-linear-gradient(left, #fff5f3 0%, #fee4dc 24%, #fff5f3 36%, #fee4dc 46%, #fff5f3 52%, #fee4dc 58%, #fee4dc 67%, #fff5f3 100%);
  background-size: 100%;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  background-size: 300%;
  -webkit-animation: font-shine 10s infinite;
  animation: font-shine 10s infinite;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.shine-font3 {
  background: -webkit-linear-gradient(left, #cd5243 0%, #f7c2bb 25%, #cd5243 52%, #f7c2bb 75%, #cd5243 100%);
  background-size: 100%;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  background-size: 300%;
  -webkit-animation: font-shine 10s infinite;
  animation: font-shine 10s infinite;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Theme */
#loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 9000000;
}

#loading .load-center {
  position: absolute;
  top: 40.5%;
  left: 0;
  right: 0;
  margin: 0 auto;
  text-align: center;
}

#loading .load-center .line-wrap {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 60px;
  height: 60px;
  padding: 30px;
}

#loading .load-center .line-wrap .line {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 60px;
  height: 3px;
  background: #cb1b45;
  -webkit-animation: spin -1.5s infinite ease;
  -o-animation: spin -1.5s infinite ease;
  animation: spin 1.5s infinite ease;
}

#loading .load-center .line-wrap .line:nth-of-type(2) {
  -webkit-animation-delay: -0.1s;
  -o-animation-delay: -0.1s;
  animation-delay: -0.1s;
}

#loading .load-center .line-wrap .line:nth-of-type(3) {
  -webkit-animation-delay: -0.2s;
  -o-animation-delay: -0.2s;
  animation-delay: -0.2s;
}

#loading .load-center .line-wrap .line:nth-of-type(4) {
  -webkit-animation-delay: -0.3s;
  -o-animation-delay: -0.3s;
  animation-delay: -0.3s;
}

#loading .load-line {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: 20px;
  width: 0%;
  height: 1px;
  background-color: #cb1b45;
  -webkit-transition: all 1s 0.5s ease;
  -o-transition: all 1s 0.5s ease;
  transition: all 1s 0.5s ease;
}

