.navigation_trigger {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 50px;
  height: 50px;
  background-color: transparent;
  position: absolute;
  right: 0px;
  top: 0;
  z-index: 1030;
  -webkit-transition: all 0.2s cubic-bezier(0.18, 0.72, 0.61, 1.15);
  transition: all 0.2s cubic-bezier(0.18, 0.72, 0.61, 1.15);
  cursor: pointer;
}

.navigation_trigger .line1,
.navigation_trigger .line2,
.navigation_trigger .line3 {
  position: absolute;
  width: 48px;
  height: 3px;
  background-color: #341210;
  left: 0px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transform-origin: left top;
  -ms-transform-origin: left top;
  transform-origin: left top;
}

.navigation_trigger .line1 {
  top: 1px;
}

.navigation_trigger .line2 {
  top: 20px;
}

.navigation_trigger .line3 {
  top: 37px;
}

.navigation_trigger:hover .line1 {
  width: 25px;
  -webkit-transform: translate(0, 20px) rotate(-45deg);
  -ms-transform: translate(0, 20px) rotate(-45deg);
  transform: translate(0, 20px) rotate(-45deg);
}

.navigation_trigger:hover .line3 {
  width: 25px;
  -webkit-transform: translate(1px, -18px) rotate(45deg);
  -ms-transform: translate(1px, -18px) rotate(45deg);
  transform: translate(1px, -18px) rotate(45deg);
}

.navigation_trigger.nav_in {
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
}

.menu {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  height: auto;
}

.menu.nav_in {
  height: 100%;
}

.contents_nav {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
}

.contents_nav.nav_in {
  z-index: 1000;
}

.contents_nav.nav_in .nav_list {
  opacity: 1;
}

.contents_nav .nav_close {
  position: absolute;
  top: 60px;
  right: 55px;
  width: 50px;
  height: 50px;
  z-index: 60;
  cursor: pointer;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  -webkit-transition: all 0.2s cubic-bezier(0.18, 0.72, 0.61, 1.15);
  transition: all 0.2s cubic-bezier(0.18, 0.72, 0.61, 1.15);
}

.contents_nav .nav_close .line1,
.contents_nav .nav_close .line2 {
  position: absolute;
  width: 48px;
  height: 3px;
  background-color: #dbc089;
  left: 0;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

.contents_nav .nav_close .line1 {
  top: 20px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.contents_nav .nav_close .line2 {
  top: 20px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.contents_nav .nav_close:hover .line1,
.contents_nav .nav_close:hover .line2 {
  background-color: #fff;
}

.contents_nav .nav_close.nav_in {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.contents_nav .nav_close.nav_in_end:hover {
  -webkit-transform: rotate(90deg) scale(1.2);
  -ms-transform: rotate(90deg) scale(1.2);
  transform: rotate(90deg) scale(1.2);
}

.contents_nav .nav_list {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  -webkit-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  opacity: 0;
  box-sizing: border-box;
}

.contents_nav .nav_list.nav_in {
  opacity: 1;
  margin: 10% auto 0;
}

.contents_nav .nav_list .item {
  width: 100%;
  display: block;
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
}

.contents_nav .nav_list .item a {
  color: #dbc089;
  text-decoration: none;
  text-align: center;
  font-size: 40px;
  font-size: 4rem;
  font-family: "Noto Serif TC";
  display: block;
  letter-spacing: 0.1rem;
  width: 100%;
  max-width: 320px;
  margin: 0 auto 10px;
  padding: 0 0 60px;
  background: url("../images/navbg.png") no-repeat bottom center;
  background-size: 100%;
  transition: all 0.3s;
}

.contents_nav .nav_list .item a:hover {
  color: #fff;
  background: url("../images/navbg-hover.png") no-repeat bottom center;
  background-size: 100%;
  transition: all 0.3s;
}

.contents_nav .nav_list .item.privacyLink a {
  font-size: 26px;
  font-size: 2.6rem;
}

.nav_cover {
  background-color: #000;
  position: fixed;
  width: 100%;
  height: 0;
  left: 0;
  top: -50%;
  z-index: -1;
  opacity: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
}

.nav_cover.nav_in {
  height: 200%;
  opacity: 0.8;
  z-index: 50;
}

.navigation_off .contents_nav,
.navigation_off .nav_cover {
  display: none;
}

