html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}
* {
  box-sizing: border-box;
}
html,
body {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
  color: #170000;
}
html {
  position: relative;
}
body {
  font-family: "Noto Sans TC", "Source Sans Pro", "Microsoft JhengHei",
    "微軟正黑體", sans-serif;
  font-family: "微軟正黑體" \0; //for ie
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
  overflow-x: hidden;
  background: $body-bg;
}
body.fixed {
  height: 100%;
  overflow: hidden;
}
main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
nav ul {
  list-style: none;
}
a {
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  background: transparent;
  outline: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input,
select,
button,
textarea {
  vertical-align: middle;
  outline: 0;
}
img {
  vertical-align: top;
  max-width: 100%;
}
.clear {
  clear: both;
  width: 0;
  height: 0px;
  margin: 0;
  padding: 0;
  border: none;
  overflow: hidden;
}

.fb-page,
.fb-page span,
.fb-page span iframe[style] {
  width: 100% !important;
}
.ie8-wrap {
  display: none;
}

button {
  cursor: pointer;
}

/*通用全站都有的寫法*/
*:link,
*:visited,
*:hover,
*:active,
*:focus {
  transition: color 0.25s linear, background-color 0.25s linear,
    border-color 0.25s linear, padding 0.15s linear, filter 0.15s linear,
    opacity 0.15s linear, transform 0.25s linear;
}


.break {
  display: none;
}

.pc {
  display: block;
}
.mb {
  display: none;
}
// pad layout
@media screen and (max-width: 767px) {
  .mb {
    display: block;
  }
  .pc {
    display: none;
  }
}